$font-family: 'Source Sans Pro', "Arial", sans-serif;
$base-color: #f17228;
$primary-color: #ffb30e;
$white-color: #ffffff;
$drop-shadow: #808080;
$btn-active: #da3802;

@mixin setFontSize($weight, $size, $line-height) {
  font-weight: $weight;
  font-size: $size;
  line-height: $line-height;
}